<template>
  <v-sheet>
    <v-data-table
      v-for="(items, category) in filteredData"
      :key=category
      disable-sort
      :headers=headers(category)
      hide-default-footer
      item-class="class"
      item-key="fullName"
      :items=items
    >
    <template v-slot:top>
      <p class="title text-capitalize pt-8 pl-4">
        {{ t(category) }}
      </p>
    </template>

    <template v-slot:footer>
      <hr />
    </template>

    <template v-slot:item.years="{ value }">
      <v-chip
        v-for="year in value"
        :key=year
        color=primary
        dark
        small
        class="mr-1"
      >
        {{ year }}
      </v-chip>
    </template>

    <template v-slot:item.year="{ value }">
      <v-chip color=primary dark small>
        {{ value }}
      </v-chip>
    </template>

    <template
      v-if="category.toLowerCase() === 'studies'"
      v-slot:item.school="{ value }"
    >
      <a
        v-if=value.url
        :href=value.url
        target="_blank"
        class=text-decoration-none
      >
        {{ value.name }}
      </a>
      <span v-else>{{ value.name }}</span>
    </template>

    <template
      v-else-if="category.toLowerCase() === 'taught'"
      v-slot:item.course="{ value }"
    >
      <a
        v-if=value.url
        :href=value.url
        target="_blank"
        class=text-decoration-none
      >
        {{ value.name }}
      </a>
      <span v-else>{{ value.name }}</span>
    </template>

    <template
      v-else-if="category.toLowerCase() === 'trainings'"
      v-slot:item.entity="{ value }"
    >
      <a
        :href=value.url
        target="_blank"
        class=text-decoration-none
      >
        {{ value.name }}
      </a>
    </template>

    <template
      v-else-if="category.toLowerCase() === 'languages'"
      v-slot:item.school="{ value }"
    >
      <span v-if="value">{{ value.name }}
        <span v-if="value.chapter">- {{ value.chapter }}</span>
      </span>
    </template>
    <template
      v-else-if="category.toLowerCase() === 'certifications'"
      v-slot:item.name="{ item, value }"
    >
      <v-icon v-text="`$${item.icon}`" class="mr-2" />
      {{ value }}
    </template>
    </v-data-table>
    <v-divider />
  </v-sheet>
</template>

<script>
export default {
  name: 'cvEducation',
  props: {
    data: {
      type: Array[Object],
      default: () => [],
    },
  },
  mounted() {
    Object.keys(this.data).forEach((category) => {
      this.data[category].forEach((entry) => {
        Object.defineProperty(
          entry,
          'fullName',
          {
            value: entry.level
              ? `${entry.name} - ${entry.level}`
              : entry.name,
          },
        );
      });
    });
  },
  computed: {
    filteredData() {
      return Object.fromEntries(
        Object.keys(this.data).map((category) => (
          [category, this.data[category].filter((item) => !item.hidden)]
        )),
      );
    },
  },
  methods: {
    headers(category) {
      const dataHeaders = [...new Set(
        this.data[category].flatMap((arr) => Object.keys(arr)),
      )];
      return dataHeaders.map((value) => {
        if (value !== 'class' && value !== 'hidden' && value !== 'icon') {
          const header = this.t(value);
          return {
            sortable: false,
            text: header.charAt(0).toUpperCase() + header.slice(1),
            value,
          };
        }
        return {};
      });
    },
  },
};
</script>

<style lang="scss">
  .v-data-table-header {
    background: var(--v-primary-base);
    >tr>th {
      color: white !important;
    };
  }
  .main {
    font-weight: bolder;
  }
</style>
